import { Component } from '@angular/core';
import { LoginEOSService } from '@cryptolions/ulm-eosio';
import { ContractApiService } from 'src/app/core/services/contact.services/contract.api.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent {
  public account_name = '';
  constructor(public contract: ContractApiService, public loginEOSService: LoginEOSService){
    loginEOSService.loggedIn.subscribe(res => {
      this.account_name = loginEOSService.accountName;
      this.contract.changeUserName(loginEOSService.accountName);
      this.contract.changeApi(loginEOSService.eosioWalletType.toUpperCase())
    });
  }
  login(){
    console.log('g');
  }
}
